import React from 'react'
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';
import { getProfitLoss } from '../../api/accounting/reports'
import Loader from '../../App/components/Reusables/Loader'
import Num from '../../App/components/Reusables/Num';
import Jhilikka from '../../App/components/Reusables/Jhilikka';



function ProfitLoss(props) {
    const [loading, setLoading] = React.useState(true)
    const [expenses, setExpenses] = React.useState({})
    const [revenues, setRevenues] = React.useState({})

    React.useEffect(() => {

        const controller = new AbortController();
        (async () => {
            await getProfitLoss({ action: 'get', filter: 'none', start: 0, end: 999999 }, controller.signal)
                .then(async (res) => {
                    if (res.json.status) {
                        const revs = res.json.revenue;
                        revs.meta_data.sum_current_amount *= -1;
                        console.log(revs);
                        for (let x in revs.sub_headers) {
                            revs.sub_headers[x].meta_data.sum_current_amount *= -1;
                            for (let y in revs.sub_headers[x].accounts) {
                                revs.sub_headers[x].accounts[y].current_amount *= -1;
                            }
                        }
                        await setExpenses({ ...res.json.expense })
                        await setRevenues({ ...revs })
                        await setLoading(false)
                    } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
                }).catch((err) => {
                    console.log(err)
                });
        })()
        return () => controller.abort()
    }, [])

    const _expand = (index, side) => {
        console.log(index, side, 'hare tea we go');
        if (side === 'expenses') {
            const tempExpenses = expenses;
            tempExpenses.sub_headers[index].isExpanded = false;
            setExpenses({ ...tempExpenses })
        }

        if (side === 'revenues') {
            const tempRevenues = revenues;
            tempRevenues.sub_headers[index].isExpanded = false;
            setRevenues({ ...tempRevenues })
        }
    }

    const _collapse = (index, side) => {
        if (side === 'expenses') {
            const tempExpenses = expenses;
            tempExpenses.sub_headers[index].isExpanded = true;
            setExpenses({ ...tempExpenses })
        }

        if (side === 'revenues') {
            const tempRevenues = revenues;
            tempRevenues.sub_headers[index].isExpanded = true;
            setRevenues({ ...tempRevenues })
        }
    }


    // console.log(expenses)
    if (!loading) {

        var List = (input, type) => {
            return input.sub_headers.map((aRow, index) => {
                let ButtonElement = null;
                if (!aRow.isExpanded) {
                    ButtonElement =
                        <i
                            className="feather icon-minus-square text-c-green f-14 pl-1 pr-1"
                            style={{ color: '#10a9f5', cursor: 'pointer' }}
                            onClick={(e) => _collapse(index, type)}
                        />
                } else {
                    ButtonElement = <i
                        className="feather icon-plus-square text-c-green f-14 pl-1 pr-1"
                        style={{ color: '#10a9f5', cursor: 'pointer' }}
                        onClick={(e) => _expand(index, type)}
                    />
                }

                const AccountsList = aRow.accounts.map((ac, inx) => {
                    if (ac.current_amount === 0) return null;
                    return <tr onClick={() => props.history.push('/account/' + ac.uuid)} key={inx + 'r'} className='balanceSheetChildRow'>
                        <th></th>
                        <td className='balanceSheetChildData'  >
                            {ac.name}
                        </td>
                        <td className='balanceSheetChildData'><Num>{ac.current_amount}</Num></td>
                    </tr >
                })
                if (aRow.meta_data.sum_current_amount === 0) return null;
                return [<tr key={index} >
                    <th></th>
                    <td>
                        {aRow.name}
                        {aRow.accounts.length > 0 ? ButtonElement : null}
                    </td>
                    <td><Num>{aRow.meta_data.sum_current_amount}</Num></td>
                </tr>,
                ...(!aRow.isExpanded ? AccountsList : [])
                ]
            })
        }
    }


    if (loading) return <Loader />;
    return (
        <>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Profit and Loss Account</h4>
                <div>
                    <span></span>
                </div>
            </Row>
            <br />
            <Row>
                <Col>
                    <Card>
                        {/* <h5 style={{ textAlign: 'center', marginTop: '30px' }}>{(new Date()).toDateString()}</h5> */}
                        <Card.Body>
                            <Table className="accountingReportTable" hover responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Revenues</th>
                                        <th>Amount</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {List(revenues, 'revenues')}
                                </tbody>
                                <thead>
                                    <tr >
                                        <th></th>
                                        <th><u>Total Revenues</u></th>
                                        <th><u><Num>{revenues.meta_data.sum_current_amount}</Num></u></th>
                                    </tr>
                                    <tr>
                                        <th>-</th>
                                        <td>-</td>
                                        <td>-</td>
                                    </tr>
                                    <tr>
                                        <th>#</th>
                                        <th>Expenses</th>
                                        <th>Amount</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {List(expenses, 'expenses')}
                                </tbody>
                                <thead>
                                    <tr>
                                        <th></th>
                                        <th><u>Total Expenses</u></th>
                                        <th><u>{expenses.meta_data.sum_current_amount}</u></th>
                                    </tr>
                                    <tr className="ProfitLossLastRow">
                                        <th></th>
                                        <th>Net Profit</th>
                                        <th><Num>{revenues.meta_data.sum_current_amount}</Num> - <Num>{expenses.meta_data.sum_current_amount}</Num> = <Num>{revenues.meta_data.sum_current_amount - expenses.meta_data.sum_current_amount}</Num></th>
                                    </tr>
                                </thead>

                            </Table>
                        </Card.Body>
                    </Card >
                </Col>
            </Row>
        </>
    )
}

export default ProfitLoss
